import * as types from './actionType'
import axios from 'axios'



const getJobReq = () => {
    return {
        type : types.GET_JOB_REQUEST
    }
}

const getJobSuccess = (payload) => {
    return {
        type : types.GET_JOB_SUCCESS,
        payload
    }
}

const getJobFailure = () => {
    return {
        type : types.GET_JOB_FAILURE
    }


}

//for filter
const getFilterJobReq = () => {
    return {
        type : types.GET_FILTERJOB_REQUEST
    }
}

const getFilterJobSuccess = (payload) => {
    return {
        type : types.GET_FILTERJOB_SUCCESS,
        payload
    }
}

const getFilterJobFailure = () => {
    return {
        type : types.GET_FILTERJOB_FAILURE
    }


}



//

//posting JObs
const postReq = () => {
    return {
        type: types.UPDATE_JOB_REQUEST
    }
}

const postSuccess = (payload) => {
    return {
        type: types.UPDATE_JOB_SUCCESS,
        payload
    }
}
const postFailure = () => {
    return {
        type : types.UPDATE_JOB_FAILURE
    }
}

//getData
export const getData = () => {
    return(dispatch) => {
        dispatch(getJobReq());
        axios.get("https://glacial-bastion-32625.herokuapp.com/masai_jobs").then((res) => dispatch(getJobSuccess(res.data))).catch((e) => dispatch(getJobFailure()))              

    }
}

//postData
export const postData = (payload) => {
    return(dispatch) => {
        dispatch(postReq());
        axios.post(`https://glacial-bastion-32625.herokuapp.com/masai_jobs`,payload).then((res) => dispatch(postSuccess(res.data))).catch((e) => dispatch(postFailure()))              

    }
}

//for filteer

export const getFilterData = (serachBy,filter) => {
    return(dispatch) => {
        dispatch(getFilterJobReq());
        axios.get(`https://glacial-bastion-32625.herokuapp.com/masai_jobs?${serachBy}=${filter}`).then((res) => dispatch(getFilterJobSuccess(res.data))).catch((e) => dispatch(getFilterJobFailure()))              

    }
}
