import React from 'react'
import { Link } from 'react-router-dom'

const AfLogin = () => {
  return (
    <div>
        <Link to='/admin' style={{color:"blue"}}>GO TO ADMIN SECTION</Link>

        <br /><br />

        <Link to='/user' style={{color:"red"}}>GO TO USER SECTION</Link>

        
    </div>
  )
}

export default AfLogin