import React from 'react'
import styles from '../styles/card.module.css'

const Card = (props) => {
    // console.log(props)
  return (
    <div className={styles.shadow}>
        <div>
            <img className={styles.img} src="data:image/jpeg;base64," alt="" />
        </div>

        <div style={{display:"flex",gap:"20px",color:"grey",justifyContent:"center"}}>
            <p>{props.postedAt}</p>
            .
            <p>{props.contract}</p>
        </div>

        <h2 style={{fontWeight:"bold"}}>{props.position}</h2>
        <p style={{color:"grey"}}>{props.company}</p>

        <h4 style={{color:"blue"}}>{props.location}</h4>
    </div>
  )
}

export default Card