import * as types from './actionType';

const initialState = {
    jobs : [],
    isLoading: false,
    isError: false,
}


export const reducer = (state = initialState,action) => {
    const {type,payload} = action

    switch(type) {
        case types.GET_JOB_REQUEST : {
            return {
                ...state,
                isLoading : true,
                isError : false
            }
        }
        case types.GET_JOB_SUCCESS : {
            return {
                ...state,
                jobs : payload,
                isLoading : false,
                isError : false
            }
        }
        case types.GET_JOB_FAILURE : {
            return {
                ...state,
                isLoading : false,
                isError : true
            }
        }
        case types.GET_FILTERJOB_REQUEST : {
            return {
                ...state,
                isLoading : true,
                isError : false

            }
        }
        case types.GET_FILTERJOB_SUCCESS : {
            return {
                ...state,
                jobs : payload,
                isLoading : false,
                isError : false
            }
        }
        case types.GET_FILTERJOB_FAILURE : {
            return {
                ...state,
                isLoading : false,
                isError : true
            }
        }
        default :
        return state
    }
};