import React from 'react'
import { Button, Input } from '@chakra-ui/react'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { postData } from '../Redux/action'

const Formpage = () => {
    const dispatch = useDispatch()
    const [name,setName] = useState("");
    const [position, setPosition] = useState("")
    const [contract,setContract] = useState("");
    const [location,setLocation] = useState("")

    const handleClick = () => {
        let payload = {
            company : name,
            position : position,
            contract : contract,
            location : location

        }
        dispatch(postData(payload))
        alert("Job posted successfully")
    }
  return (
    <div>
        <label>Company name : </label>
        <Input type="text" width={"200px"} onChange={(e) => setName(e.target.value)}/>
        <br />
        <label>Position : </label>
        <Input type="text"  width={"200px"} onChange={(e) => setPosition(e.target.value)} />
        <br />
        <label>Contract : </label>
        <Input type="text"  width={"200px"} onChange={(e) => setContract(e.target.value)} />
        <br />
        <label>Location : </label>
        <Input type="text"  width={"200px"} onChange={(e) => setLocation(e.target.value)} />
        <br />
        <Button onClick={handleClick}>SUBMIT</Button>
    </div>
  )
}

export default Formpage