import React from "react";
import { useState, useEffect } from "react";
import {getData, getFilterData} from '../Redux/action'

import { useDispatch, useSelector } from "react-redux";
import Card from "../Components/Card";
import styles from '../styles/user.module.css'
import { Button, Input } from "@chakra-ui/react";

const User = () => {
  const [search,setSearch] = useState("")
  const [contract,setContract] = useState("")
  const dispatch = useDispatch();
  const jobs = useSelector((state) => state.jobs);

  const [data, setData] = useState(jobs);
  const handleSearch = () => {
    // console.log(search)
    dispatch(getFilterData("company",search))
  }

  const handleContarct = (e) => {
    if(e == "part")
    {
      dispatch(getFilterData("contract","Part Time"))
    }
    else if(e === "full")
    {
      dispatch(getFilterData("contract", "Full Time"))
    }
    else if(e === "Freelance")
    {
      dispatch(getFilterData("contract", "Freelance"))
    }
  }

  useEffect(() => {
    dispatch(getData());
  }, []);
//   console.log(jobs)

  return (
    <>
    <div>
      <Input width="md" placeholder="Search By Company Name" onChange={(e) => setSearch(e.target.value)} /> 
      <Button onClick={handleSearch}>Search</Button>
    </div>
    <div>
      <label>Filter by contract :-</label>
      <select onChange={(e) => handleContarct(e.target.value)}>
        <option value="">none</option>
        <option value="part">Part-time</option>
        <option value="full">Full-time</option>
        <option value="Freelance">Freelance</option>
      </select>
    </div>


    <div className={styles.grid}>
        {
            jobs.map((item) => (
                <Card {...item} key={item.id}/>
            ))
        }

    </div>
    </>
  );
};

export default User;
